.loader {
  height: 10rem;
  width: 5rem;
  position: relative;
}

.lightsaber {
  position: absolute;
  width: 4px;
  height: 12px;
  background-color: #666;
  border-radius: 1px;
  bottom: 50%;
}

.lightsaber.lsLeft {
  left: 0;
}

.lightsaber.lsRight {
  right: 0;
}

.lightsaber:before {
  position: absolute;
  content: ' ';
  display: block;
  width: 2px;
  height: 25px;
  max-height: 1px;
  left: 1px;
  top: 1px;
  background-color: #fff;
  border-radius: 1px;
  -webkit-transform: rotateZ(180deg);
  transform: rotateZ(180deg);
  -webkit-transform-origin: center top;
  -ms-transform-origin: center top;
  transform-origin: center top;
}

.lightsaber:after {
  position: absolute;
  content: ' ';
  display: block;
  width: 2px;
  height: 2px;
  left: 1px;
  top: 4px;
  background-color: #fff;
  border-radius: 50%;
}

.lsParticles {
  position: absolute;
  left: 42px;
  top: 10px;
  width: 1px;
  height: 5px;
  background-color: rgb(51, 51, 51, 0);
  -webkit-transform: rotateZ(0deg);
  transform: rotateZ(0deg);
}

.lightsaber.lsGreen:before {
  -webkit-animation: showlightgreen 1s ease-in-out infinite 1s;
  animation: showlightgreen 1s ease-in-out infinite 1s;
}

.lightsaber.lsRed:before {
  -webkit-animation: showlightred 1s ease-in-out infinite 1s;
  animation: showlightred 1s ease-in-out infinite 1s;
}

.lightsaber.lsLeft {
  -webkit-animation: fightleft 1s ease-in-out infinite 1s;
  animation: fightleft 1s ease-in-out infinite 1s;
}

.lightsaber.lsRight {
  -webkit-animation: fightright 1s ease-in-out infinite 1s;
  animation: fightright 1s ease-in-out infinite 1s;
}

.lsParticles.lsPart1 {
  -webkit-animation: particles1 1s ease-out infinite 1s;
  animation: particles1 1s ease-out infinite 1s;
}

.lsParticles.lsPart2 {
  -webkit-animation: particles2 1s ease-out infinite 1s;
  animation: particles2 1s ease-out infinite 1s;
}

.lsParticles.lsPart3 {
  -webkit-animation: particles3 1s ease-out infinite 1s;
  animation: particles3 1s ease-out infinite 1s;
}

.lsParticles.lsPart4 {
  -webkit-animation: particles4 1s ease-out infinite 1s;
  animation: particles4 1s ease-out infinite 1s;
}

.lsParticles.lsPart5 {
  -webkit-animation: particles5 1s ease-out infinite 1s;
  animation: particles5 1s ease-out infinite 1s;
}

@-webkit-keyframes showlightgreen {
  0% {
    max-height: 0;
    box-shadow: 0 0 0 0 #87c054;
  }

  5% {
    box-shadow: 0 0 4px 2px #87c054;
  }

  10% {
    max-height: 22px;
  }

  80% {
    max-height: 22px;
  }

  85% {
    box-shadow: 0 0 4px 2px #87c054;
  }

  100% {
    max-height: 0;
    box-shadow: 0 0 0 0 #87c054;
  }
}

